<template>
    <div>
        <v-app-bar id="appBar" app dark :elevation="showScrollToTopFab?4:0"
                   :color="showScrollToTopFab?'primary':'transparent'">
            <v-app-bar-nav-icon @click="$router.back()">
                <v-icon>mdi-arrow-left</v-icon>
            </v-app-bar-nav-icon>
            <v-toolbar-title>{{showScrollToTopFab?blogArticle.title:null}}</v-toolbar-title>
            <v-spacer/>
            <v-tooltip v-if="false" bottom>
                <template v-slot:activator="{ on }">
                    <v-btn v-on="on" icon @click="$vuetify.theme.dark = !$vuetify.theme.dark">
                        <v-icon v-if="!$vuetify.theme.dark">mdi-brightness-7</v-icon>
                        <v-icon v-else>mdi-brightness-4</v-icon>
                    </v-btn>
                </template>
                <span>{{$vuetify.theme.dark?"日间":"夜间"}}</span>
            </v-tooltip>
            <v-menu v-if="$vuetify.breakpoint.smAndDown" origin="top center" offset-overflow absolute
                    transition="scale-transition">
                <template v-slot:activator="{ on }">
                    <v-btn icon dark v-on="on">
                        <v-icon>mdi-cellphone-link</v-icon>
                    </v-btn>
                </template>
                <div style="width: 150px;height: 150px">
                    <VueQr :margin="10" :text="qrLinkText" style="width: 100%;height: 100%"/>
                </div>
            </v-menu>
            <v-tooltip v-else bottom>
                <template v-slot:activator="{ on }">
                    <div v-on="on">
                        <v-menu origin="top center" offset-overflow absolute transition="scale-transition">
                            <template v-slot:activator="{ on }">
                                <v-btn icon dark v-on="on">
                                    <v-icon>mdi-cellphone-link</v-icon>
                                </v-btn>
                            </template>
                            <div style="width: 150px;height: 150px">
                                <VueQr :margin="10" :text="qrLinkText" style="width: 100%;height: 100%"/>
                            </div>
                        </v-menu>
                    </div>
                </template>
                <span>在其他设备上继续阅读</span>
            </v-tooltip>
            <v-tooltip bottom>
                <template v-slot:activator="{ on }">
                    <v-btn v-on="on" icon @click="drawer = !drawer">
                        <v-icon v-if="drawer">mdi-close</v-icon>
                        <v-icon v-else>mdi-table-of-contents</v-icon>
                    </v-btn>
                </template>
                <span>{{drawer?"关闭":"目录"}}</span>
            </v-tooltip>
        </v-app-bar>
        <!--目录-->
        <v-navigation-drawer app v-model="drawer" right :permanent="drawer && !$vuetify.breakpoint.smAndDown"
                             :temporary="$vuetify.breakpoint.smAndDown" mini-variant-width="200">
            <v-treeview return-object dense :items="catalogue" activatable transition
                        @update:active="updateActive" hoverable/>
        </v-navigation-drawer>
        <v-img :src="blogArticle.imgUrl===null?'https://cdn.vuetifyjs.com/images/cards/docks.jpg':blogArticle.imgUrl"
               style="height: 400px; margin-top: -70px">
            <template v-slot:placeholder>
                <v-row class="fill-height ma-0" align="center" justify="center" style="background-color: #1976d2">
                    <v-progress-circular indeterminate color="grey lighten-5"/>
                </v-row>
            </template>
        </v-img>
        <!--文章标题-->
        <v-content class="pe-0">
            <v-card-title class="display-1 justify-center white--text"
                          style="height: 300px; width: 100%; margin-top: -470px;font-weight: bold">
                {{blogArticle.title}}
            </v-card-title>
        </v-content>
        <!--文章详情-->
        <v-card id="card" :tile="$vuetify.breakpoint.smAndDown"
                :raised="!$vuetify.breakpoint.smAndDown"
                :flat="$vuetify.breakpoint.smAndDown"
                max-width="1000px" class="mx-auto pt-1"
                style="margin-top: -180px">
            <EditorMD :pre-set-content="blogArticle.content"/>
            <div id="likeFab"></div>
            <div v-if="blogArticle.canReward" class="d-block text-center pt-5">
                <v-tooltip bottom>
                    <template v-slot:activator="{ on }">
                        <div v-on="on">
                            <v-menu origin="center" nudge-left="45" nudge-top="65" transition="scale-transition">
                                <template v-slot:activator="{ on }">
                                    <v-btn fab color="accent" v-on="on">
                                        <v-icon>mdi-thumb-up-outline</v-icon>
                                    </v-btn>
                                </template>
                                <v-img style="width: 150px" src="../assets/payCode.png"/>
                            </v-menu>
                        </div>
                    </template>
                    <span>赞赏</span>
                </v-tooltip>
            </div>
            <v-card-text class="d-inline-block text-end">
                发布于{{blogArticle.uploadTimeString}}<br/>
                {{blogArticle.uploadTimeString===blogArticle.updateTimeString?null:"最后更新于"+blogArticle.updateTimeString}}
            </v-card-text>
            <div v-if="blogArticle.typeString!==undefined && blogArticle.typeString!==null">
                <v-divider/>
                <div class="d-inline">
                    <v-icon class="pa-2">mdi-view-list</v-icon>
                    <router-link :to="{name:'/index/type',params:{paramType: blogArticle.typeString}}">
                        {{blogArticle.typeString}}
                    </router-link>
                </div>
            </div>
            <div v-if="blogArticle.tags!==undefined && blogArticle.tags.length">
                <v-divider/>
                <div class="d-flex">
                    <v-icon class="pa-2">mdi-tag-multiple</v-icon>
                    <v-chip-group column>
                        <v-chip v-for="tag in blogArticle.tags" :key="tag"
                                :to="{name:'/index/tag',params:{'paramTag':tag}}">
                            {{tag}}
                        </v-chip>
                    </v-chip-group>
                </div>
            </div>
            <!--转载声明-->
            <div v-if="blogArticle.original">
                <v-alert border="left" text class="ms-5 me-5" colored-border color="#e00000">
                    本文链接：{{API.UI_BASE_URL+$route.fullPath}}
                </v-alert>
            </div>
            <v-divider/>
        </v-card>
        <!--文章切换-->
        <v-card tile id="cardBottomNavigationBar"
                :class="$vuetify.breakpoint.smAndDown?'d-flex ps-10 pe-10':'d-flex ps-10 pe-10 mt-10'">
            <!--TODO 切换文章-->
            <v-card flat tile class="pa-5" style="width: 50%" @click="drawer=!drawer">
                <v-icon class="d-inline">mdi-arrow-left</v-icon>
                <v-card-subtitle class="d-inline pb-0">上一篇</v-card-subtitle>
                <v-card-title class="d-block pt-0 pb-0">上一篇文章的标题</v-card-title>
            </v-card>
            <v-card flat tile class="pa-5 d-block justify-end" style="width: 50%;text-align: end"
                    @click="drawer=!drawer">
                <v-card-subtitle class="d-inline pb-0">下一篇</v-card-subtitle>
                <v-icon class="d-inline">mdi-arrow-right</v-icon>
                <v-card-title class="d-block pt-0 pb-0">下一篇文章的标题</v-card-title>
            </v-card>
        </v-card>
        <Footer/>
        <v-snackbar v-model="snackbar">
            已自动定位到你此前阅读位置
            <v-btn color="pink" text @click="$vuetify.goTo(0)">从头开始</v-btn>
        </v-snackbar>
        <v-fab-transition>
            <v-btn v-show="showScrollToTopFab" fab color="accent" class="ma-5"
                   style="position: fixed;right: 0;bottom: 0;z-index: 10" @click="$vuetify.goTo(0)">
                <v-progress-circular size="56" width="2" rotate="-90" :value="percentOfRead * 100">
                    <v-icon>mdi-chevron-up</v-icon>
                </v-progress-circular>
            </v-btn>
        </v-fab-transition>
    </div>
</template>

<script>
    import Footer from "@/components/Footer"
    import VueQr from "vue-qr"
    import EditorMD from "@/components/admin/EditorMD";

    export default {
        name: "ArticleDetail",
        components: {EditorMD, Footer, VueQr},
        props: ["id"],
        data: () => {
            return {
                blogArticle: {},
                showScrollToTopFab: false,
                drawer: false,
                catalogue: [
                    {id: 1, name: "1"},
                    {id: 2, name: "2"},
                    {id: 3, name: "3"},
                    {id: 4, name: "4", children: [{id: 5, name: "4-1"}, {id: 6, name: "4-2"}]},
                    {id: 7, name: "5"},
                    {id: 8, name: "6"},
                    {
                        id: 9,
                        name: "7",
                        children: [{id: 10, name: "7-1"}, {id: 11, name: "7-2"}, {
                            id: 12,
                            name: "7-3",
                            children: [{id: 13, name: "7-3-1"}]
                        }]
                    }
                ],
                percentOfRead: 0.0,
                realPercentOfRead: 0.0,
                totalHeight: Number,
                snackbar: false,
                qrLinkText: String
            }
        },
        methods: {
            getArticle: function () {
                console.log(this.$route)
                if (this.$route.params.articleFromPre !== undefined) {
                    this.blogArticle = this.$route.params.articleFromPre
                } else {
                    let _this = this
                    this.axios.get(this.API.URL.QUERY_ARTICLE_BY_ID + this.id, {headers: {'Content-Type': 'multipart/form-data'}})
                        .then(function (result) {
                            if (result.data.success) {
                                _this.blogArticle = result.data.data
                                document.title = _this.blogArticle.title
                            }
                        }, function (error) {
                            console.log(error)
                        })
                }
            },
            updateActive(array) {
                console.log(array)
            },
            handleScroll() {
                //滚动条偏移量
                let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
                //文章开始偏移量
                let cardOffsetTop = document.getElementById("card").offsetTop
                //文章结束偏移量
                let likeFabOffsetTop = document.getElementById("likeFab").offsetTop
                //是否显示滑倒顶部的FAB
                this.showScrollToTopFab = scrollTop >= cardOffsetTop
                // console.log("scrollTop", scrollTop, "cardOffsetTop", cardOffsetTop, "likeFabOffsetTop", likeFabOffsetTop, "totalHeight", this.totalHeight, "window.screen.height", window.screen.availHeight)
                //计算实际阅读百分比
                if (scrollTop >= cardOffsetTop) {
                    if (scrollTop + cardOffsetTop <= likeFabOffsetTop - cardOffsetTop) {
                        this.percentOfRead = ((scrollTop - cardOffsetTop) / (likeFabOffsetTop - cardOffsetTop - cardOffsetTop - cardOffsetTop)).toFixed(2)
                    } else {
                        this.percentOfRead = 1.0
                    }
                } else {
                    this.percentOfRead = 0.0
                }
                //计算网页百分比
                this.realPercentOfRead = (scrollTop) / this.totalHeight
                this.qrLinkText = this.API.UI_BASE_URL + this.$route.path.toString() + "?realPercentOfRead=" + this.realPercentOfRead
                // console.log("this.percentOfRead", this.percentOfRead, "this.realPercentOfRead", this.realPercentOfRead)
            },
        },
        created: function () {
            this.getArticle()
            this.axios.post(this.API.URL.INCREASE_ONE_CLICK_COUNT_ARTICLE + this.id)
        },
        mounted: function () {
            this.qrLinkText = this.API.UI_BASE_URL + this.$route.path.toString()
            this.totalHeight = document.body.scrollHeight
            console.log("this.totalHeight", this.totalHeight)
            //监听滚动事件，然后用handleScroll这个方法进行相应的处理
            window.addEventListener('scroll', this.handleScroll)
            let realPercentOfRead = this.$route.query.realPercentOfRead
            if (realPercentOfRead !== undefined && realPercentOfRead !== 0.0) {
                this.$vuetify.goTo(realPercentOfRead * this.totalHeight)
                this.snackbar = true
            }
        },
        watch: {},
        destroyed() {
            //移除滚动事件，然后用handleScroll这个方法进行相应的处理
            window.removeEventListener('scroll', this.handleScroll, false)
        }
    }
</script>

<style scoped>

</style>